.NavBar {
    height: 70px;
    display: flex;
    justify-content: space-between;
}
.zeroLogo {
    width: auto;
    height: 100%;
    margin-left: 10px;
}
ul.NavBar {
    display: flex;
    height: 100%;
    align-items: center;
    margin-right: 20px;    
}
ul.NavBar > li {
    margin-right: 20px;
    font-family: 'Nanum Gothic', sans-serif;
    font-size: 16px;
    
}
li > a {
    color: gray;
    text-decoration: none;
    
}