.card {
    display: flex;
    box-sizing: border-box;
    background-color: initial;
    width: 100%;
    height: 100px;
    padding: 15px;
    border-bottom: 1px solid rgb(237, 236, 236);
    border-right: 1px solid rgb(237, 236, 236);
    border-top: 1px solid rgb(249, 249, 249);
    border-left: 1px solid rgb(249, 249, 249);
    border-radius: 10px;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 10px;
   color: black;
}

.card:hover {
    
    cursor: pointer; 
    background: rgb(188,0,255);
background: -moz-linear-gradient(326deg, rgba(188,0,255,1) 0%, rgba(255,120,0,1) 100%);
background: -webkit-linear-gradient(326deg, rgba(188,0,255,1) 0%, rgba(255,120,0,1) 100%);
background: linear-gradient(326deg, rgba(188,0,255,1) 0%, rgba(255,120,0,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#bc00ff",endColorstr="#ff7800",GradientType=1);
color: white;
    
   background-color: rgb(235, 235, 235);
}
.card > img {
    display: inline-block;
    margin-right: 10px;
    height: 90%;
    width: 70px;
    border-radius: 10px;
    object-fit: cover;
    justify-self: flex-start;
    align-self: center;
}
.card > a {
    text-decoration: none;
    
}

.card p {
    font-size: 14px;  
}

.cardDate {
    display: block;
    justify-self: flex-end;
    flex-grow: 1;
}
.cardDate > p {
    text-align: right;
}
.cardDiv {
    justify-self: flex-start;
    align-self: center;
    flex-grow: 3;
    text-align: left;
}
.card > button {
    border: none;
    background-color: white;
}
.cardTitle {
    margin-bottom: 10px;
    
    font-size: 20px;
}

@media screen and (max-width: 1000px) {
    .cardText {
      display: none;
    }
    .card{
        background: rgb(188,0,255);
        background: -moz-linear-gradient(326deg, rgba(188,0,255,1) 0%, rgba(255,120,0,1) 100%);
        background: -webkit-linear-gradient(326deg, rgba(188,0,255,1) 0%, rgba(255,120,0,1) 100%);
        background: linear-gradient(326deg, rgba(188,0,255,1) 0%, rgba(255,120,0,1) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#bc00ff",endColorstr="#ff7800",GradientType=1);
        color: white;
        background-color: rgb(235, 235, 235);
    }
    .cardTitle {
        font-size: 15px;
    }
  }