
.buttonMain {
    padding: 20px;
    width: auto;
    height: auto;
    background: rgb(255,0,0);
    background: linear-gradient(357deg, rgba(255,0,0,1) 0%, rgba(255,143,0,1) 100%);
    color: white;
    font-size: 20px;
    font-weight: bold;
    border: none;
    border-radius: 30px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
button:hover {
    cursor: pointer;
}
.arraySection {
    margin-top: 10px;
    

padding: 100px;
max-width: 100vw;

}
.MainComp {
    max-width: 100%;
}
.gradient {
    margin-top: 60px;
    margin-bottom: 0px;
    width: 100vw;
    height: 40px;
    background: rgb(255,255,255);
    background: -moz-linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(245,245,245,1) 100%);
    background: -webkit-linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(245,245,245,1) 100%);
    background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(245,245,245,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#f5f5f5",GradientType=1);
}

