.playerHolder {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 350px;
    max-width: 100%;
    background: rgb(131,58,180);
background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
}
.playerWrapper {
    height: 100%;
    width: 60%;
}
.pageParagraph {
     margin-top: 20px;
     padding: 30px;
     
     font-size: 25px;
}
@media screen and (max-width: 1000px) {
    .playerWrapper {
        width: 100%;
    }
  }